import * as model from './model.js';
import searchView from './views/searchView.js';
import resultsView from './views/resultsView.js';
import recipeView from './views/recipeView.js';
import paginationView from './views/paginationView.js';
import bookmarksView from './views/bookmarksView.js';

import { accordion } from './module/accordion.js';
import { bookmarksPopup, menuPopup } from './module/popup.js';
import { calcCalories } from './module/calc.js';

import AOS from 'aos';

const controlRecipes = async function () {
  try {
    const id = window.location.hash.slice(1);
    const query = model.state.search.query;

    if (!id) return;

    // Update results view to mark selected search results
    resultsView.render(model.getSearchResultsPage(), query);
    bookmarksView.render(model.state.bookmarks);
    bookmarksView.renderNumBookmarks(model.state.bookmarks);

    // Spinner works before load recipe
    recipeView.renderSpinner();

    // Load recipe
    await model.loadRecipe(id);

    // Render recipe
    recipeView.render(model.state.recipe);

    // Init accordion
    accordion.init('.accordion');

    // Smooth scroll to recipe
    recipeView.smoothScroll();
  } catch (error) {
    // Render error message
    recipeView.renderError();
  }
};

const controlSearchResults = async function () {
  try {
    // Get search query from form input
    const query = searchView.getQuery();

    if (!query) return;

    // Spinner works before load search results
    resultsView.renderSpinner();

    // Load search results
    await model.loadSearchResults(query);

    // Render result
    resultsView.render(model.getSearchResultsPage(), query);

    // Render pagination
    paginationView.render(model.state.search);
  } catch (error) {
    console.log(error);
    // Render error message
    resultsView.renderError();
  }
};

const controlPagination = function (page) {
  const query = model.state.search.query;
  // Render NEW result
  resultsView.render(model.getSearchResultsPage(page), query);
  // Render NEW pagination
  paginationView.render(model.state.search);
};

const controlAddBookmark = function () {
  // Add/remove bookmark
  if (!model.state.recipe.bookmarked) {
    model.addBookmark(model.state.recipe);
  } else {
    model.deleteBookmark(model.state.recipe.id);
  }

  // Render bookmarks
  bookmarksView.render(model.state.bookmarks);
  bookmarksView.renderNumBookmarks(model.state.bookmarks);
};

const controlBookmarks = function () {
  model.restoreBookmarks();
  bookmarksView.render(model.state.bookmarks);
  bookmarksView.renderNumBookmarks(model.state.bookmarks);
};

const controlDeleteBookmarks = function () {
  model.clearBookmarks();
  bookmarksView.render(model.state.bookmarks);
  bookmarksView.renderNumBookmarks(model.state.bookmarks);
};

const controlPopup = function () {
  bookmarksPopup.init('.popup-bookmarks', '.open-bookmarks');
  menuPopup.init('.popup-menu', '.open-menu');
};

const controlMenuResults = async function () {
  try {
    const query = calcCalories.calcResult();

    if (!query) return;

    // Spinner works before load search results
    resultsView.renderSpinner();

    // Load search results
    await model.loadSearchResults(query);

    // Render result
    resultsView.render(model.getSearchResultsPage(), query);

    // Render pagination
    paginationView.render(model.state.search);
  } catch (error) {
    resultsView.renderError();
  }
};

const init = function () {
  AOS.init();
  controlPopup();
  recipeView.addHandlerRender(controlRecipes);
  bookmarksView.addHandlerRender(controlBookmarks);
  bookmarksView.addHandlerDeleteBookmarks(controlDeleteBookmarks);
  recipeView.addHandlerAddBookmark(controlAddBookmark);
  searchView.addHandlerSearch(controlSearchResults);
  paginationView.addHandlerClick(controlPagination);
  calcCalories.init();
  calcCalories.addHandlerClick(controlMenuResults);
};

init();
