import { menuPopup } from './popup';

class Calc {
  constructor() {
    // Calc calories
    this.holder = document.querySelector('.calc-form');
    this.inputs = null;
    this.sex = null;
    this.height = null;
    this.weight = null;
    this.age = null;
    this.activity = null;
    this.validate = false;

    // Slider
    this.slider = null;
    this.track = null;
    this.slides = null;
    this.slideWidth = null;
    this.btnPrev = null;
    this.btnNext = null;
    this.slideClass = '.slider-slide';
    this.disadledClass = 'disabled';
    this.curSlide = 0;
    this.maxSlides = null;
  }

  init() {
    this.findElements();
    // this.setCalc();
    this.setSlider();
    this.addEventListeners();
  }

  findElements() {
    // Calc
    this.inputs = this.holder.querySelectorAll('input');
    // Slider
    this.slider = this.holder.querySelector('.slider');
    this.track = this.slider.querySelector('.slider-track');
    this.slides = this.slider.querySelectorAll(this.slideClass);
    this.btnPrev = this.slider.querySelector('.btn-prev');
    this.btnNext = this.slider.querySelector('.btn-next');
    this.maxSlides = this.slides.length;
    this.slideWidth = this.slider.offsetWidth;
  }

  addEventListeners() {
    this.btnPrev.addEventListener('click', this.moveToPrevSlide.bind(this));
    this.btnNext.addEventListener('click', this.moveToNextSlide.bind(this));
    this.inputs.forEach(input => {
      input.addEventListener('change', this.validateData.bind(this));
      input.addEventListener('input', this.validateData.bind(this));
    });
    this.holder.addEventListener('keydown', function (e) {
      if (e.key === 'Enter') {
        e.preventDefault();
      }
    });
    // this.holder.addEventListener('submit', this.calcResult.bind(this));
  }

  addHandlerClick(handler) {
    this.holder.addEventListener('submit', function (e) {
      e.preventDefault();
      handler();
      e.target.reset();
    });
  }

  validateData(e) {
    e.preventDefault();
    const inputName = e.target.getAttribute('name');
    const inputValue = e.target.value;
    const parent = e.target.closest('.form-block');

    if (e.target.type === 'number') {
      const value = +inputValue;
      const minNum = +e.target.min;
      const maxNum = +e.target.max;

      if (typeof value === 'number' && value >= minNum && value <= maxNum) {
        parent.classList.remove('error');
        this.validate = true;
        this.takeData(inputName, inputValue);
      } else {
        parent.classList.add('error');
        this.validate = false;
      }
    } else {
      if (inputValue) {
        parent.classList.remove('error');
        this.validate = true;
        this.takeData(inputName, inputValue);
      }
    }
    this.toggleClass();
    this.checkSlides();

    if (inputName === 'activity') {
      this.holder
        .querySelector('.btn-submit')
        .classList.remove(this.disadledClass);
    }
  }

  calcResult() {
    let result;
    if (this.sex === 'male') {
      result = Math.round(
        (88.36 + 13.4 * this.weight + 4.8 * this.height - 5.7 * this.age) *
          this.activity
      );
    } else {
      result = Math.round(
        (447.6 + 9.2 * this.weight + 3.1 * this.height - 4.3 * this.age) *
          this.activity
      );
    }
    menuPopup.hide();
    menuPopup.toggleClass();
    setTimeout(() => {
      this.curSlide = 0;
      this.setSlider();
    }, 1000);

    return result;
  }

  takeData(name, value) {
    switch (name) {
      case 'sex':
        this.sex = value;
        break;
      case 'age':
        this.age = +value;
        break;
      case 'height':
        this.height = +value;
        break;
      case 'weight':
        this.weight = +value;
        break;
      case 'activity':
        this.activity = +value;
        break;
      default:
        console.log('Error');
        break;
    }
  }

  setSlider() {
    this.slides.forEach(slide => (slide.style.width = `${this.slideWidth}px`));
    this.track.style.width = `${this.slideWidth * this.maxSlides}px`;
    this.moveToSlide(this.curSlide);
  }

  moveToSlide(num) {
    this.track.style.transform = `translate3d(${
      -this.slideWidth * num
    }px, 0, 0)`;
    this.checkData();
    this.checkSlides();
  }

  moveToNextSlide(e) {
    e.preventDefault();
    this.curSlide++;
    this.moveToSlide(this.curSlide);
  }

  moveToPrevSlide(e) {
    e.preventDefault();
    this.curSlide--;
    this.moveToSlide(this.curSlide);
  }

  checkSlides() {
    if (this.curSlide === this.maxSlides - 1) {
      this.btnNext.classList.add(this.disadledClass);
    }

    if (this.curSlide === 0) {
      this.btnPrev.classList.add(this.disadledClass);
    } else {
      this.btnPrev.classList.remove(this.disadledClass);
    }
  }

  toggleClass() {
    if (this.validate) {
      this.btnNext.classList.remove(this.disadledClass);
    } else {
      this.btnNext.classList.add(this.disadledClass);
    }
  }

  checkData() {
    const curEl = this.slides[this.curSlide];
    const elements = curEl.querySelectorAll('input');
    if (elements.length > 1) {
      const checkedEl = [];
      [...elements].forEach(el => {
        if (el.checked) {
          checkedEl.push(el);
        }
      });
      if (checkedEl.length > 0) {
        this.validate = true;
        if (checkedEl[0].name === 'activity') {
          this.holder
            .querySelector('.btn-submit')
            .classList.remove(this.disadledClass);
        }
      } else {
        this.validate = false;
      }
    } else {
      const value = +elements[0].value;
      const min = +elements[0].min;
      const max = +elements[0].max;
      value >= min && value <= max
        ? (this.validate = true)
        : (this.validate = false);
    }
    this.toggleClass();
  }
}

const CalcPlugin = {
  create: () => new Calc(),
};

export const calcCalories = CalcPlugin.create();
