import { API_URL, API_KEY, RES_PER_PAGE } from './config.js';
import { makeRequest } from './helpers.js';

export const state = {
  search: {
    query: '',
    results: [],
    page: 1,
    resultsPerPage: RES_PER_PAGE,
  },
  recipe: {},
  bookmarks: [],
};

const createRecipeObj = function (recipe) {
  return {
    id: recipe.id,
    title: recipe.title,
    image: recipe.image,
    servings: recipe.servings,
    cookingTime: recipe.readyInMinutes,
    sourceUrl: recipe.sourceUrl,
    ingredients: recipe.nutrition.ingredients,
    nutrition: recipe.nutrition.nutrients,
    summary: recipe.summary,
    instructions: recipe.instructions,
  };
};

export const loadRecipe = async function (id) {
  try {
    const data = await makeRequest(
      `${API_URL}recipes/${id}/information?apiKey=${API_KEY}&includeNutrition=true`
    );

    state.recipe = createRecipeObj(data);

    if (state.bookmarks.some(bookmark => bookmark.id === +id)) {
      state.recipe.bookmarked = true;
    } else {
      state.recipe.bookmarked = false;
    }
  } catch (error) {
    throw error;
  }
};

export const loadSearchResults = async function (query) {
  try {
    state.search.query = query;
    const request =
      typeof query !== 'number'
        ? `${API_URL}recipes/complexSearch?apiKey=${API_KEY}&query=${query}&number=100`
        : `${API_URL}mealplanner/generate?apiKey=${API_KEY}&timeFrame=day&targetCalories=${query}`;

    const data = await makeRequest(`${request}`);
    const res = typeof query !== 'number' ? data.results : data.meals;

    state.search.results = res.map(recipe => {
      return {
        id: recipe.id,
        title: recipe.title,
        image: recipe.image,
      };
    });
    state.search.page = 1;
  } catch (error) {
    console.log(error);
    throw error;
  }
};

export const getSearchResultsPage = function (page = state.search.page) {
  state.search.page = page;
  const start = (page - 1) * state.search.resultsPerPage;
  const end = page * state.search.resultsPerPage;
  return state.search.results.slice(start, end);
};

const storeBookmarks = function () {
  localStorage.setItem('bookmarks', JSON.stringify(state.bookmarks));
};

export const restoreBookmarks = function () {
  const storage = localStorage.getItem('bookmarks');

  if (storage) state.bookmarks = JSON.parse(storage);
};

export const addBookmark = function (recipe) {
  // Add bookmark
  state.bookmarks.push(recipe);
  state.recipe.bookmarked = true;

  storeBookmarks();
};

export const deleteBookmark = function (id) {
  // Delete bookmark
  const filterArr = state.bookmarks.filter(bookmark => bookmark.id !== id);
  state.bookmarks = filterArr;

  // Mark current recipe as NOT bookmarked
  if (id === state.recipe.id) state.recipe.bookmarked = false;

  storeBookmarks();
};

export const clearBookmarks = function () {
  localStorage.clear('bookmarks');
  state.bookmarks = [];
};
