import View from './View.js';

export default class ReviewView extends View {
  _parentElement = '';

  _generateMarkup() {
    return `
      <h2>Search results for <span class="query">${this._createSearchMessage()}</span></h2>
      ${this._data.map(this._generateMarkupPreview).join(' ')}
    `;
  }

  _createSearchMessage() {
    return typeof this._query === 'number'
      ? `${this._query} kcal menu`
      : this._query;
  }

  _generateMarkupPreview(res) {
    const id = +window.location.hash.slice(1);

    return `
    <div data-aos="fade-in" data-aos-duration="300"
      data-aos-easing="ease-in-out">
      <a href="#${res.id}" class="recipe-block ${
      res.id === id ? 'active' : ''
    }">
    ${
      res.image
        ? `<div class="img-block">
      <img src="${res.image}" alt="${res.title}" />
    </div>
    `
        : ``
    }
    <div class="text-block">
      <h3>${res.title}</h3>
    </div>    
          </a>
    </div>
         
    `;
  }
}
