import View from './View.js';

class PaginationView extends View {
  _parentElement = document.querySelector('.pagination');

  addHandlerClick(handler) {
    this._parentElement.addEventListener('click', function (e) {
      e.preventDefault();
      const btn = e.target.closest('a');

      if (!btn) return;

      const page = +btn.dataset.page;
      handler(page);
    });
  }

  _generateMarkup() {
    const curPage = this._data.page;
    const numPages = Math.ceil(
      this._data.results.length / this._data.resultsPerPage
    );

    // Many pages
    if (numPages > 1) {
      return `
        <li class="${
          curPage === 1 && numPages > 1 ? 'disabled' : ''
        }"><a href="#" data-page='${curPage - 1}'>Prev</a></li>
        <li>
            <div class="count-block">
                <span class="current-page">${curPage}</span>
                <span class="count-devider">/</span>
                <span class="total-page">${numPages}</span>
            </div>
        </li>
        <li class="${
          curPage === numPages && numPages > 1 ? 'disabled' : ''
        }"><a href="#" data-page='${curPage + 1}'>Next</a></li>
      `;
    }
    // Only one page
    return '';
  }
}
export default new PaginationView();
