import View from './View.js';

class RecipeView extends View {
  _parentElement = document.querySelector('.recipe');
  _message = '';
  _errorMessage = 'We could not find that recipe. Please try another one!';

  addHandlerRender(handler) {
    ['hashchange', 'load'].forEach(ev => window.addEventListener(ev, handler));
  }

  addHandlerAddBookmark(handler) {
    this._parentElement.addEventListener('click', function (e) {
      e.preventDefault();
      const btn = e.target.closest('.btn-bookmark');

      if (!btn) return;

      btn.classList.toggle('bookmarked');
      handler();
    });
  }

  _generateMarkup() {
    return `
      <div data-aos="fade-in" data-aos-duration="300" data-aos-easing="ease-in-out">
          <div class="img-block">
            <img src="${this._data.image}" alt="${this._data.title}" />
        </div>
        <h1>${this._data.title}</h1>
        <div class="info-block">
          <ul class="recipe-info">
              <li>
                    <i class="icon-people"></i>
                    ${this._data.servings} ${
      this._data.servings > 1 ? 'people' : 'person'
    }
              </li>
              ${
                this._data.cookingTime
                  ? `<li><i class="icon-time"></i>${this._data.cookingTime} min</li>`
                  : ''
              }
          </ul>
          <a href="#" class="btn-bookmark ${
            this._data.bookmarked ? 'bookmarked' : ''
          }"><i class="icon-bookmark-o"></i></a>
        </div>
        <ul class="accordion">
            <li>
                <a class="opener" href="#">Ingredients</a>
                <div class="slide">
                    <ul class="ingredients-list">
                    ${this._data.ingredients
                      .map(this._generateMarkupIngredient)
                      .join(' ')}
                    </ul>
                </div>
            </li>
            <li>
                <a class="opener" href="#">Nutritional information</a>
                <div class="slide">
                    <table>
                        <tr>
                            <th>Name</th>
                            <th>Amount</th>
                            <th>Percent of daily needs</th>
                        </tr>
                        ${this._data.nutrition
                          .map(this._generateMarkupNutrition)
                          .join(' ')}
                    </table>
                </div>
            </li>
            <li>
                <a class="opener" href="#">Summary</a>
                <div class="slide">
                  <p>${this._data.summary}</p>
                  ${this._data.instructions}
                </div>
            </li>
        </ul>
      </div>
`;
  }

  _generateMarkupIngredient(ing) {
    return `
        <li>${ing.amount} ${ing.unit} ${ing.name}</li>
    `;
  }

  _generateMarkupNutrition(nut) {
    return `
        <tr>
            <th>${nut.name}</th>
            <td>${nut.amount} ${nut.unit}</td>
            <td>${nut.percentOfDailyNeeds}%</td>
        </tr>
    `;
  }

  smoothScroll() {
    // Need to fix
    this._parentElement.scrollIntoView({ behavior: 'smooth' });
  }
}

export default new RecipeView();
